/* #region header */
.header {
  // display: none !important;

  background-image: url(../images/header-bg-mobile1.png);
  background-size: cover;
  background-position: center;


  &__content {
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    margin: 0;
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    line-height: 100%; /* 36px */

    @include onTablet{
      font-size: 48px;
    }
  }

  &__bottom {
    padding-bottom: 40px;


    @include onTablet {
      padding-bottom: 65px;
    }

  }
  }

